}

@font-face {
    font-family: 'poppins';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
}
html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
}

p {
    font-family: var(--secondary-font);
    font-size: 22px;
    line-height: 2.5em;
}

.container {
    --box-shadow: 6px 6px 15px 2px rgba(0, 0, 0, 0.5);
    --text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.5);
    --background-color: rgba(255, 255, 255, 0.8);
    --border-radius: 15px;
    --primary-font: 'ragna-runes';
    --secondary-font: 'poppins';
    box-sizing: border-box;
    background-attachment: fixed;
    margin: 0;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 50px;
    padding: 50px;
    overflow-y: scroll;
}

.name-header {
    margin: 0;
    padding: 0;
    font-family: 'ragna-runes';
    font-size: 80px;
    letter-spacing: 15px;
    color: white;
    text-shadow: var(--text-shadow);
}

.content-tile {
    background-color: var(--background-color);
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
    width: 60%;
}

.nav-bar {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.nav-button {
    width: 20%;
    padding: 20px;
    font-family: 'ragna-runes';
    font-weight: bold;
    letter-spacing: 10px;
    border: none;
    outline: none;
    background: none;
    font-size: 34px;
    transition: text-shadow .25s ease;
}

.nav-button:hover {
    text-shadow: var(--text-shadow);
}

.home-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    -webkit-box-sizing: border-box;
    padding: 15px 50px;

}

.home-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 2;
    gap: 50px;
}

.home-title {
    grid-column: 1/2;
    grid-row: 1/2;
    align-self: center;
    font-family: var(--primary-font);
    text-shadow: var(--text-shadow);
    font-size: 50px;
    letter-spacing: 15px;
    line-height: 2.5em;
}

.intro-paragraph{ 
    grid-column: 1 / 3;
    grid-row: 2 / 3;
    align-self: center;
    justify-self: center;
}

.home-image {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    height: auto;
    width: 80%;
    align-self: center;
    justify-self: center;
}

.beer-tile {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto auto;
    justify-content: space-between;
    align-items: center;
}

.beer-abv,
.beer-description,
.intro-paragraph {
    font-family: var(--secondary-font);
}

.beer-name {
    font-family: var(--primary-font);
    letter-spacing: 7px;
    font-size: 30px;
    font-weight: 600;
}

.beer-abv {
    text-align: end;
    font-size: 26px;
    font-weight: 600;

}

.beer-description {
    grid-column: 1 / 3;
    grid-row: 2 / 3;
}

hr {
    width: 100%;
}

.contact-container {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 2;
}

.contact-title {
    grid-column: 1/2;
    grid-row: 1/2;
    align-self: center;
    font-family: var(--primary-font);
    text-shadow: var(--text-shadow);
    font-size: 60px;
    letter-spacing: 15px;
}

.contact-image {
    justify-self: start;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
}

.contact-description {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    padding-top: 50px;
    padding-right: 50px;
}

.info-container {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    padding-left: 50px;
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
